.swiper-choice {
    max-width: 700px;
    display: flex;
    flex-direction: column;
    margin: auto;
}

.swiper-choice__title {
    margin-bottom: 65px;
}

.choice-slider-wrapper {
    width: 300px;
    margin-bottom: 65px;
    margin-left: auto;
    margin-right: auto;
}

.choice-slider-wrapper :global(.swiper-slide-shadow){
    /* box-shadow: none !important; */
    border-radius: 110px;
}

.choice-slider-wrapper :global(.swiper-slide){
    /* opacity: .8; */
}

.choice-slider-wrapper :global(.swiper-slide-active){
    opacity: 1;
}

.choice-slide {
    height: 410px;
    background: radial-gradient(111.45% 290.39% at 18.74% 45.45%, #441500 0%, #AA3500 100%)
        /* warning: gradient uses a rotation that is not supported by CSS and may not behave as expected */
    ;
    border-radius: 110px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.choice-slide_default{
    padding: 20px;
}

.choice-slide__default-content{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 25px;
}

.swiper-choice__submit {
    width: 300px;
    margin-left: auto;
    margin-right: auto;
}

.choice-slide__pic {
    position: relative;
    height: 200px;
    width: 100%;
    margin-bottom: 20px;
}

.choice-slide__pic img {
    max-width: 240px;
    height: auto;
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    margin: auto;
}

.choice-slide__title {
    max-width: 220px;
}

.choice-slider-container {
    position: relative;
    max-width: 600px;
    margin-left: auto;
    margin-right: auto;
    width: 100%;
}

.cards-slider__prev{
    width: 60px;
    position: absolute;
    top: 0;
    bottom: 0;
    margin: auto;
    left: 0;
}

.cards-slider__next{
    width: 60px;
    position: absolute;
    top: 0;
    bottom: 0;
    margin: auto;
    right: 0;
}