.check-list {
    display: flex;
    flex-direction: column;
    width: 770px;
    align-items: center;
    margin: auto;
}

.check-list__counter {
    width: 100px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #fff;
    border-radius: 20px;
    font-size: 14px;
    font-weight: 500;
    margin-bottom: 15px;
}

.check-list__title {
    margin-bottom: 30px;
}

.check-list__answers {
    margin-bottom: 100px;
    display: flex;
    flex-direction: column;
    width: 100%;
    gap: 10px;
}

.check-list__submit {
    width: 300px;
}

.check-list-answer {
    display: flex;
    padding: 16px;
    border-radius: 38px;
    background: linear-gradient(0deg, rgba(255, 255, 255, 0.1), rgba(255, 255, 255, 0.1)),
        radial-gradient(111.45% 290.39% at 18.74% 45.45%, #441500 0%, #AA3500 100%);
}

.check-list-answer__content {
    background-color: rgba(255, 255, 255, .1);
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 12px;
    border-radius: 27px;
    height: 54px;
    transition: .3s;
    gap: 10px;
}

.check-list-answer__content_active {
    background-color: #fff;
}

.check-list-answer__checkmark {
    width: 30px;
    height: 30px;
    display: flex;
    flex-shrink: 0;
    border-radius: 50%;
    background: #FFFFFF80;
    border: 2px solid #FFFFFF2B;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: .3s;
}

.check-list-answer__checkmark_active {
    background-color: var(--orange);
}