.test-game {
    display: flex;
    flex-direction: column;
    width: 770px;
    align-items: center;
    margin: auto;
}

.test-game__counter {
    width: 100px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #fff;
    border-radius: 20px;
    font-size: 14px;
    font-weight: 500;
    margin-bottom: 15px;
}

.test-game__title {
    margin-bottom: 30px;
}

.test-game__answers{
    margin-bottom: 100px;
    display: flex;
    flex-direction: column;
    width: 100%;
    gap: 10px;
}

.test-game__submit{
    width: 300px;
}