.button {
    display: flex;
    align-items: center;
    justify-content: center;
    /* width: max-content; */
    width: 100%;
    padding: 0 24px;
    border: none;
    /* box-shadow: 0 6px 6px rgba(0, 0, 0, .25); */
    font-size: 15px;
    font-weight: 500;
    color: #ffffff;
    transition: opacity .3s;
    text-decoration: none;
    cursor: pointer;
    /* height: 60px; */
    /* border-radius: 30px; */
    position: relative;
}

.button:hover {
    /* opacity: .7; */
}

.button_size_normal {
    height: 60px;
    border-radius: 30px;
    font-size: 16px;
}

.button_size_small {
    height: 40px;
    border-radius: 20px;
    font-size: 14px;
}

.button:disabled {
    opacity: .6;
    cursor: not-allowed;
}

.button_color_orange {
    background-color: var(--orange);
    color: #ffffff;
    box-shadow: 0px 4px 4px 0px #FFFFFF54 inset, 0px 12px 26.7px 0px #F3410E91;
}

.button_color_white {
    background-color: #ffffff;
    color: #000000;
}

.button_color_brown {
    background:
        radial-gradient(111.45% 290.39% at 18.74% 45.45%, #441500 0%, #AA3500 100%);
    color: #ffffff;
}

.button_color_transparent {
    backdrop-filter: blur(10px);
    background-color: #FFFFFF1C;
    -webkit-backdrop-filter: blur(10px);
}

.button_color_outline {
    background: #FFFFFF05;
    border: 1px solid #FFFFFF1A;
    color: #fff;
}

.button_color_gray {
    background-color: #373737;
}

.button_color_blue {
    background-color: #35A2EB;
    color: #fff;
}

.button_color_golden {
    background-image:
        linear-gradient(107.85deg, #FFFBB4 8.88%, #C38500 61.34%, #FFFFFF 101.58%);
    color: #000000;
    box-shadow: 0px 12px 26.7px 0px #C3850078, 0px 4px 4px 0px #FFFFFF54 inset;
}