.phrazle-game {
    max-width: 890px;
    display: flex;
    flex-direction: column;
    margin: auto;
}

.phrazle-game__title {
    margin-bottom: 50px;
}

.target-word {
    padding: 5px;
    background-image: linear-gradient(107.85deg, #FFFBB4 8.88%, #C38500 61.34%, #FFFFFF 101.58%);
    border-radius: 55px;
    margin-bottom: 60px;
}

.target-word__content {
    background-color: #fff;
    border-radius: 50px;
    padding: 35px;
}

.word-value {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    grid-gap: 20px;
}

.word-value__letter {
    background: #F9F4EC;
    border-radius: 25px;
    height: 120px;
    border: 1px solid #0000000A;
    background-image: url(../../../public/assets/images/days/day1/game2/question-icon.svg);
    background-repeat: no-repeat;
    background-position: center;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 32px;
    font-weight: 700;
    color: #fff;
}

.word-value__letter_active {
    background: url(../../../public/assets/images/days/day1/game2/cancel-icon.svg),
        radial-gradient(118.64% 118.64% at 50% 50%, #AA3500 0%, #441500 100%);
    background-repeat: no-repeat;
    background-position: calc(100% - 15px) calc(100% - 15px), center;
    background-size: auto, cover;
    border: none;
}

.word-letters {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    grid-gap: 20px;
    padding-left: 45px;
    padding-right: 45px;
    margin-bottom: 65px;
}

.word-letters__letter {
    background:
        radial-gradient(118.64% 118.64% at 50% 50%, #AA3500 0%, #441500 100%);
    background-repeat: no-repeat;
    background-position: center;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 32px;
    font-weight: 700;
    color: #fff;
    height: 120px;
    border-radius: 25px;
}

.word-letters__letter_active {
    background: #4415000F;
}

.target-word__hint{
    height: 60px;
    display: flex;
    align-items: center;
    background-color: #fff;
    border-radius: 30px;
    padding-left: 30px;
    padding-right: 30px;
    box-shadow: 0px 4px 15px 0px #0000000D;
    width: max-content;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 60px;
}

.target-word__hint_error{
    background-color: #E73F0C;
}

.target-word__submit{
    width: 300px;
    margin-left: auto;
    margin-right: auto;
}